@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&family=Dancing+Script&display=swap);

body {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 100vh;
  margin: 0;
  font-family: 'Comfortaa', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(
    to right bottom,
    #36084e,
    #532f71,
    #715496,
    #917abb,
    #b2a2e1
  );
}

html {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.centrare {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.container {
  font-family: inherit;
  padding-top: 50px;
  margin-top: 200px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 400px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  min-width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  border-radius: 5px;
  background: transparent;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 10px 0px;
}

.btn-add {
  font-family: inherit;
  margin-top: 20px;
  width: 80px;
  height: 35px;
  border-radius: 5px;
  color: white;
  background: transparent;
  border: 1px solid white;
  transition: all 0.3s ease-in;
  font-weight: bold;
  font-size: 1em;
}

.btn-add:hover {
  cursor: pointer;
  background: green;
}

.input {
  border: 1px solid white;
  border-radius: 5px;
  height: 30px;
  font-size: 1.3em;
  background: transparent;
  font-family: inherit;
  color: white;
  padding: 0;
  padding: 5px 5px;
  text-align: center;
}

.input:focus {
  outline: none;
  border: 1px solid black;
}

.paragraph-gol {
  text-align: center;
  color: white;
  font-family: inherit;
  font-size: 1.2em;
  font-weight: 600;
}

.list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: inherit;
}

.listElement {
  color: white;
  font-size: 1.4em;
  margin-top: 5px;
  grid-row: 2;
  grid-column: 1;
  padding: 0px 10px;
}

.delete-btn {
  letter-spacing: 0.1em;
  color: white;
  margin-left: 140px;
  font-family: inherit;
  background-image: linear-gradient(
    to right bottom,
    #f25d6a,
    #ef6069,
    #ec6268,
    #e86568,
    #e56767
  );
  border: 1px solid white;
  padding: 10px;
  border-radius: 5px;
  transition: all 0.3s ease-in;
  font-weight: bold;
}

.delete-btn:hover {
  cursor: pointer;
  background: transparent;
}

