.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 300px;
  height: fit-content;
  padding: 10px 0px;
}

.btn-add {
  font-family: inherit;
  margin-top: 20px;
  width: 80px;
  height: 35px;
  border-radius: 5px;
  color: white;
  background: transparent;
  border: 1px solid white;
  transition: all 0.3s ease-in;
  font-weight: bold;
  font-size: 1em;
}

.btn-add:hover {
  cursor: pointer;
  background: green;
}

.input {
  border: 1px solid white;
  border-radius: 5px;
  height: 30px;
  font-size: 1.3em;
  background: transparent;
  font-family: inherit;
  color: white;
  padding: 0;
  padding: 5px 5px;
  text-align: center;
}

.input:focus {
  outline: none;
  border: 1px solid black;
}

.paragraph-gol {
  text-align: center;
  color: white;
  font-family: inherit;
  font-size: 1.2em;
  font-weight: 600;
}
