.list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: inherit;
}

.listElement {
  color: white;
  font-size: 1.4em;
  margin-top: 5px;
  grid-row: 2;
  grid-column: 1;
  padding: 0px 10px;
}

.delete-btn {
  letter-spacing: 0.1em;
  color: white;
  margin-left: 140px;
  font-family: inherit;
  background-image: linear-gradient(
    to right bottom,
    #f25d6a,
    #ef6069,
    #ec6268,
    #e86568,
    #e56767
  );
  border: 1px solid white;
  padding: 10px;
  border-radius: 5px;
  transition: all 0.3s ease-in;
  font-weight: bold;
}

.delete-btn:hover {
  cursor: pointer;
  background: transparent;
}
